import React from 'react';
import { HashRouter as Router,Redirect, Route,Switch } from 'react-router-dom'
import Header from './pages/Header';
import routes from './router.config';

export function PrivateRoute({ component: Com, ...rest }) {
	return (
		<Route
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com {...rest}/>
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>	
	);
}
export const RouteWithSubRoutes=(props)=>{
	//将routes属性传递给子组件
	// console.log("routewithsubroutes:",props.routes);
	if(props.pri){
		return <PrivateRoute {...props}/>

	}
	else{
	return (<Route 
		render={()=>{
			//渲染返回的组件和传递routes属性给子组件
			return <props.component/>
		}
		}
		/>)
	}
}
const App = () => {
	return (<Router>
		<Header />
		<Switch>
			{
				routes.map((route)=>{
					return(
						<RouteWithSubRoutes {...route}/>
					) 
				})
			}
		</Switch>
	</Router>)
}

export default App;

